.down {
    display: none;
}

.down .form-control {
    border: 1px solid lightgray !important;
    border-radius: 0 !important;
    margin-bottom: 10px;
}

.Attandence_Request_Recieve {
    width: 100%;
    padding: 20px;
    font-size: 12px !important;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid {
    display: grid;
    grid-template-columns: 30fr 70fr;
    grid-column-gap: 30px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Left .DIV1 {
    display: flex;
    border-radius: 5px !important;
box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-bottom: 10px;
    height: max-content;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Left .DIV1 input {
    width: 100%;
    height: 40px;
    border: 0;
    border-radius: 0 !important;
    font-size: 12px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Left .DIV1 i {
    padding: 10px;
    cursor: pointer;
    background-color: #ffff;
    font-size: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Left .att_requests {
    height: calc( 100vh - 85px);
    overflow: auto;
    padding: 0 3px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right {
    display: grid;
    grid-template-columns: 40fr 60fr;
    grid-column-gap: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right1 {
    transition: .5s;
    animation: popups .2s linear 1 forwards alternate;
    opacity: 0;
    transform: scale(0.8);
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right1 .Att_Emp_Info {
    height: max-content;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color: #ffff;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right1 .Att_Emp_Info img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right1 .Emp_request_Details {
    overflow-y: auto;
    overflow-x: hidden;
    padding: 0 5px;
    height: 30vh;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right1 .Emp_request_Details .requests{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 20px;
    margin-bottom: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox {
    display: grid;
    grid-template-columns: 25fr 25fr 25fr 25fr;
    grid-column-gap: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave0 {
    background-color: #ddf5f5 !important;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave1 {
    background-color: #fbe2dd !important;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave2 {
    background-color: #d2edff !important;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave3 {
    background-color: #fef0d3 !important;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color: #ffff;
    border-radius: 5px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bolder;
    padding: 25px 0;
    transition: .5s;
    animation: popups .2s linear 1 forwards alternate;
    opacity: 0;
    transform: scale(0.8);
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p {
    margin-bottom: 0;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p:first-child {
    font-size: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox {
box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color: #ffff;
    border-radius: 5px;
    padding: 20px;
    transition: .5s;
    animation: popups .2s linear 1 forwards alternate;
    opacity: 0;
    transform: scale(0.8);
    margin-bottom: 20px;
}

.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox .BottomBox_info div {
    display: flex;
    margin-bottom: 10px;
}
.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox .BottomBox_info div p{
    margin-bottom: 0;
}
.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox .BottomBox_info .BottomBox_info_Grid {
    display: grid;
    grid-template-columns: 25fr 25fr 50fr;
    grid-column-gap: 10px;
}
.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox .BottomBox_info .BottomBox_info_Grid .BottomBox_Grid_Div{
    display: flex;
    align-items: center;
    justify-content: center;
}
.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_BottomBox .BottomBox_info .BottomBox_info_Grid .BottomBox_Grid_Div img{
    width: 200px;
    height: 200px;
}
.Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Decision_Div {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 20px;
    display: flex;
    justify-content: end;
    align-items: center;
}
@keyframes popups {
    50% {
        opacity: .5;
        transform: scale(1.1);
        z-index: -1 !important;
    }
    100% {
        opacity: 1;
        transform: scale(1);
        z-index: -1 !important;
    }
}

@media only screen and (max-width: 1200px) {
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid {
        grid-column-gap: 10px;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right {
        grid-column-gap: 10px;
        grid-template-columns: 50fr 50fr;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave {
        font-size: 10px !important;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p {
        margin-bottom: 0;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p:first-child {
        font-size: 15px;
    }
}

@media only screen and (max-width: 992px) {
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid {
        display: block;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right {
        display: block;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p:first-child {
        font-size: 30px;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave {
        font-size: 12px !important;
    }
}

@media only screen and (max-width: 768px) {
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave p:first-child {
        font-size: 15px;
    }
    .Attandence_Request_Recieve .Attandence_Request_Recieve_Grid .Details_Grid_Right .Grid_Right2 .Right2_TopBox .TopBox_Leave {
        font-size: 10px !important;
    }
}